/** General combos styles */
.combo-color {
    border: 1px solid black;
    width: 20px;
    height: 20px;
    vertical-align: top;
    color: #1d1d1d;
}
.combo-color.combo-color-form {
    cursor: pointer;
    width: 25px;
    height: 25px;
}
.combo-color.combo-color-form i {
    padding: 5px;
    color: #000000;
}

.combo-color-bg-1 {
    @include combo-color(
        $color-combo-1,
        $color-combo-1-lighter,
        $color-combo-1-text,
        $color-combo-1-border
    );
}

