.bg-default {
  background-color: #f7f7f7;
}

.blink {
  animation: blinker 1s linear infinite;
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}

.gs-inline-block {
  position: relative;
  display: inline-block;
  vertical-align: middle;
}

.vertical-hr {
  background-color: #ababab;
  height: 24px;
  margin: 0 5px;
  width: 1px;
  vertical-align: top;
}

.vertical-hr-inline {
  position: relative;
  display: inline-block;
  vertical-align: middle;
}

.gs-title {
  display: flex;
  padding: 8px 2px;
  color: #555555;
}

.gs-title-controls > a {
  margin: 2px 0;
}

.gs-profile-img {
  background: #eeeeee;
}

.gs-list-product-types > li {
  margin-right: -10px;
}

.gs-btn-no-margin {
  margin: 0px;
}

.gs-p-title {
  font-weight: 400;
  margin-bottom: 5px;
  font-size: 16px;
}

.input-group.gs-input-group {
  margin-bottom: 0;
}

.input-group-addon.gs-input-group-addon {
  width: auto;
}

.form-horizontal .control-label {
  margin-bottom: 5px;
}

.form-group .gs-inline-block {
  width: inherit;
}

#verifyEventDateMessage {
  padding-left: 10px;
}

/** Images */
.gs-image {
  background: #f7f7f7;
  border: 1px solid #e6e6e6;
}

.gs-image.gs-image-thumbnail {
  height: 64px;
  width: 64px;
  padding: 2px;
}

.no-gutter > [class*='col-'] {
  padding-right: 0;
  padding-left: 0;
}

/** General alert styles */
.item.bad .alert {
  left: auto;
}

.alert.gs-alert {
  float: none;
}

.alert.gs-alert::after {
  left: -15px;
  border-width: 12px 7px;
}

