/** Event wizard */
#smartwizard > div {
  margin: 25px 0;
}

#smartwizard > .navbar {
  display: none;
}

.event-configurable,
.event-property {
  margin-bottom: 20px;
}

.event-configurable .checkbox-inline,
.event-configurable .radio-inline,
.event-property .checkbox-inline,
.event-property .radio-inline {
  padding-left: 0px;
}

/** Select2 */
.select2-container--default .select2-selection--single .select2-selection__arrow {
  top: 6px !important;
  right: 5px !important;
}

/** Dragula */
.dragula-wrapper {
  margin: 20px 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.dragula-wrapper .dragula-container {
  height: 300px;
  width: 48%;
  border: 1px solid #e1e1e1;
  background-color: #fbfbfb;
  overflow: scroll;
  border-radius: 4px;
}

.dragula-wrapper .dragula-container.dragula-container-left {
  float: left;
}

.dragula-wrapper .dragula-container.dragula-container-right {
  float: right;
}

.dragula-wrapper .dragula-container > div,
.gu-mirror {
  margin: 10px;
  padding: 10px 15px;
  cursor: move;
  cursor: grab;
  cursor: -moz-grab;
  cursor: -webkit-grab;
  background-color: #eaeaea;
  border: 1px solid #aaaaaa;
  border-radius: 4px;
  color: #000000;
}

.dragula-wrapper .dragula-container > div input,
.gu-mirror input {
  height: 20px;
  padding: 6px 12px;
  font-size: 14px;
  text-align: right;
}

.gu-mirror {
  cursor: grabbing;
  cursor: -moz-grabbing;
  cursor: -webkit-grabbing;
}

.card {
  border-radius: 4px;
  display: inline-block;
  padding: 5px 10px;
  position: relative;
  width: 100%;
  min-height: 155px;
}

.card-header,
.card-footer {
  font-size: 18px;
  font-weight: 200;
  margin: 0px;
  padding: 0px;
}

.card-body {
  min-height: 63px;
}

.card h2 {
  font-size: 35px;
  font-weight: 400;
  margin: 10px 0;
  padding: 0px;
}

.card .fa {
  display: block;
  font-size: 1em;
}

.card dl {
  margin-bottom: 0px;
  font-size: 16px;
}

.card dt {
  font-weight: 400;
}

.card dd {
  margin-bottom: 3px;
  font-weight: 200;
}
